import { ModalCarpet, ModalInner, AbsoluteBox } from "../../styledCpns/Layout";
import { Text, LGreen, Gold, Red } from "../../styledCpns/DataDisplay";
import { CloseBtn } from "../CommonCpns";
import { useCloseModal } from "../../helpers/hooks";

export default function Info({ close }) {
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <ModalInner ref={ref} className="p-4 relative">
        <AbsoluteBox top="0.75rem" right="0.75rem">
          <CloseBtn onClick={close} />
        </AbsoluteBox>
        <Text className="mb-2" variant="h4" color="orange">
          HOW-TOs
        </Text>
        <Text>
          - <LGreen>Modify Setups</LGreen>: When you press the wrench button on
          a Setup, you're pushing a <Red>copy</Red> of it to the Calculator, so
          don't forget to save the modified copy if you want to apply the
          changes to that Setup.
        </Text>
        <Text className="mt-2">
          - <LGreen>Complex Setup</LGreen>: It is the result of combining
          Setups. On this complex, switch to teammates' Setups by pressing their
          icons. Break the complex into individual Setups again by pressing the
          link / chain icon before its name.
        </Text>
        <Text className="mt-2">
          - You can build <LGreen>teammate's Setups</LGreen> based on a saved
          Setup by hovering over teammate's icon and press the calculator button
          that would appear. Party members and Target will be the same. Some
          Modifiers will remain activated.
        </Text>
        <Text className="mt-3" variant="h5" color="red" fontWeight={700}>
          Caution
        </Text>
        <Text className="mt-2">
          - If you have <Gold>Yae Miko</Gold> in any of your saved Setups (as
          main character or teammate), please remove them as they can cause the
          App to malfunction when this character gets huge changes to buffs and
          debuffs. Sorry for the inconveniences.
        </Text>
      </ModalInner>
    </ModalCarpet>
  );
}
